<template>
  <div id="content">
    <div class="cms-item" v-for="item in items" :key="item.id">
      <img :src="`https://www.butor-mirjan24.hu/img/cms/inspirations/${item.id}.jpg`" width="370" height="218">
			<h3 class="cms-title">{{ item.meta_title }}</h3>
      <div class="cms-description"><span>{{ item.meta_description }}</span></div>
			<router-link class="btn" :to="{name: 'post', params: { id:  item.id }}">Show more</router-link>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
	name: 'Posts',
  data() {
    return {
      link: 'https://www.butor-mirjan24.hu/api/content_management_system',
      token: '9U9NSIVVWWSWUKATK942H4BPC2QWXC3M',
      cms: 3,
      items: [],
    }
  },
  mounted() {
    axios
      .get(`${this.link}?ws_key=${this.token}&output_format=JSON&filter[id_cms_category]=${this.cms}&filter[active]=[1]`)
      .then(resp => {
        resp.data.content_management_system.forEach(item => {
          axios
            .get(`${this.link}/${item.id}?ws_key=${this.token}&output_format=JSON`)
            .then(resp => {
              this.items.push(resp.data.content);
            })
        })
      })
  }
}
</script>

